/*============================================================================
	#Featured Posts
==============================================================================*/

.featured-posts {

	padding: 90px 0 95px;
	background-repeat: no-repeat;
	background-position: bottom center;

	&__header {

		margin-bottom: 40px;
	}

	.section__title {

		text-align: center;
	}

	&__body {

		margin: 0 -15px;
		display: flex;

	}

	&__col {

		margin: 0 15px;
		flex: 0 0 calc(100% / 3 - 30px);

		&:nth-child(2) {

			.post-card__image {

				top: 48px;
				left: 48px;
				width: calc(100% - 48px * 2);
				height: calc(100% - 48px * 2);
				
			}
		}
	}
}

.post-card {

	&:hover {

		.post-card__media {
		
			&::before {

				opacity: 0.8;
			}
		}

		.post-card__title {

			text-decoration: underline;
		}

		._icon {

			opacity: 1;
			transform: translate(-50%, -50%);
		}
	}

	&__wrapper-link {

		display: block;
		text-decoration: none;
	}

	&__media {

		padding-top: 100%;
		position: relative;
		margin: 0 0 0 0;
		overflow: hidden;
		background: #f5ede6;

		&::before {

			content: '';
			width: 100%;
			height: 100%;
			background: $colorMaroon;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			opacity: 0;
			transition: opacity 0.6s cubic-bezier(0.5, 1, 0.89, 1);
		}
	}

	&__image {

		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	._icon {

		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, 0);
		opacity: 0;
		fill: white;
		z-index: 2;
		width: 44px;
		height: 44px;

		transition: all 0.6s cubic-bezier(0.5, 1, 0.89, 1);
	}

	&__title {

		padding: 25px 30px 10px;
		color: white;
		font-size: 18px;
		line-height: 24px;
		font-weight: $regular;
		-webkit-box-orient: vertical;
		display: block;
		display: -webkit-box;
		text-overflow: ellipsis;
		overflow: hidden;
		-webkit-line-clamp: 2;
	}


}

