/*============================================================================
	#Catalog Page
==============================================================================*/

.catalog-page {
	
	background-position: top 86px  center;
	background-repeat: no-repeat;

	&__header {

		
		

		.container {

			display: flex;
			align-items: center;
			height: 153px;
		}
	}

	&__title {

		color: white;
		font-size: 36px;
		line-height: 1.2;
		font-weight: $semiBold;
		text-transform: uppercase;
		display: flex;
		margin: 20px 0 20px;
		-webkit-box-orient: vertical;
		display: block;
		display: -webkit-box;
		text-overflow: ellipsis;
		overflow: hidden;
		-webkit-line-clamp: 2;
	}

	.filter__form {

		padding-bottom: 15px;
	}

	&__options {

		margin-bottom: 30px;
	}

	&__pagination {

		padding: 5px 0 0;
	}

}