/*============================================================================
	#Messengers
==============================================================================*/

.messengers {

	display: flex;

	&__item {

		display: block;
		opacity: 0.3;
		transition: opacity 0.2s;
		margin-right: 10px;

		&:last-child {

			margin-right: 0;
		}

		&:hover {

			opacity: 1;
		}
	}

	&__icon {

		width: 22px;
		height: 22px;
		fill: $colorMaroon;
	}
	
}