/*============================================================================
	COMMON
==============================================================================*/
.site {
	position: relative;
	max-width: 1920px;
	min-width: 1200px;
	margin: 0 auto;
	overflow: hidden;
}

.main {

	padding-top: 86px;
}

.container {

	max-width: 1170px;
	padding: 0 15px;
	margin: 0 auto;
}

.section {
	
	&__header {

	}

	&__title {

		font-size: 48px;
		font-weight: $regular;
		color: $colorMaroon;

		&._orange {

			color: $colorOrange;
		}
	}
} 

.row {
	
}

.swiper-lazy-preloader {

	border-color: $colorMaroon;
	border-top-color: transparent;
}

.swiper-pagination-fraction {

	width: auto;
}

.swiper-counter {

	margin: 0 32px;
	color: $colorMaroon;
	opacity: .8;
	font-size: 24px;
}

.products-grid {

	&__container {

		margin: 0 -15px;
		display: flex;
		flex-wrap: wrap;
	}

	&__col {

		margin: 0 15px 30px;
		flex: 0 0 calc(100% / 3 - 30px);
	}
}

.yt-player {

	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	padding-top: 56.11%;

	&__iframe {

		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	&__button {

		position: absolute;
		left: 50%;
		top: 50%;
		width: 68px;
		height: 48px;
		margin-left: -34px;
		margin-top: -24px;
		transition: opacity .25s cubic-bezier(0,0,0.2,1);
		z-index: 1;
	}

	&__icon-bg {

		transition: fill .1s cubic-bezier(0.4,0,1,1),fill-opacity .1s cubic-bezier(0.4,0,1,1);
		fill: #212121;
		fill-opacity: .8;
	}

	&:hover {

		.yt-player__icon-bg {

			transition: fill .1s cubic-bezier(0,0,0.2,1),fill-opacity .1s cubic-bezier(0,0,0.2,1);
			fill: #f00;
			fill-opacity: 1;
		}
	}
}

.lazy-background {
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVQYV2P4//vHfwAJ4QPym4ubMgAAAABJRU5ErkJggg==) !important;
    background-repeat: repeat !important;
}

.to-top {

	width: 48px;
	height: 48px;
	flex: 0 0 auto;
	border: 1px solid $colorOrange;
	border-radius: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #f7f1eb;
	margin-top: -100px;
	margin-bottom: 50px;
	transition: margin-top .3s ease-out, transform .2s ease-out;

	._icon {

		transform: rotate(90deg);
		width: 17px;
		height: 17px;
		fill: $colorOrange;
	}

	&:hover {

		transform: scale(1.1);
	}

	&._visible {

		margin-top: 0;
	}

}

.likely {

	.likely__widget {

		margin: 0 4px;
		background: #cab3b9;
		border-radius: 2px;

		&:hover {

			background: #a0868d;
		}
	}
}

.likely-big {
	margin: 0;
}

.likely-big .likely__widget {

	height: 28px;
}

.likely-big .likely__icon {
    width: 28px;
    height: 28px;
}

.likely-big svg {
    left: 4px;
    top: 4px;
    width: 20px;
    height: 20px;
}

.no-product-found {

	
	min-height: 400px;
	display: flex;
	justify-content: center;
	padding-top: 60px;

	&__message {

		color: #450a26;
		font-size: 30px;
		font-weight: $semiBold;
	}
}
