/*============================================================================
	#Phone Dropdown
==============================================================================*/

.phone-dropdown {

	position: relative;
	background: white;

	&:hover {

		.phone-dropdown__header {
			border-color: #cccccc;
		}
	}

	&__header {

		display: flex;
		border: 1px solid transparent;
		transition: border-color .2s linear;

		.phone-dropdown__link {

			padding-right: 0;
			padding-bottom: 3px;
			padding-top: 3px;
			border-left: 1px solid transparent;
			border-right: 1px solid transparent;
		}
		
		
	}

	&__button {
		
		padding: 5px 12px;
		position: relative;

		&::before {

			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 6px 5.5px 0 5.5px;
			border-color: #a19c9f transparent transparent transparent;
			transform: translate(-50%, -50%);
		}

		&._active {
			
			&::before {
			
				border-width: 0 5.5px 6px 5.5px;
				border-color: transparent transparent #a19c9f transparent;
			}
		}
	}

	&__link {

		text-decoration: none;
		color: #787577;
		font-size: 16px;
		display: flex;
		align-items: center;
		transition: color .2s linear;
		padding: 5px 10px;

		&:hover {

			color: #2c0417;

			.phone-dropdown__icon {

				opacity: 1;
			}
		}
	}

	&__icon {

		opacity: 0.5;
		margin-right: 10px;
		transition: opacity .2s linear;
	}
	

	&__list {

		padding: 0;
		margin: 0;
		width: 100%;
		position: absolute;
		// display: none;
		visibility: hidden;
		background: white;
		border-left: 1px solid #cccccc;
		border-right: 1px solid #cccccc;
		z-index: 1;
		transform: translateY(10px);
		opacity: 0;
		transition: all 0.2s linear;

		.phone-dropdown__link {

			border-bottom: 1px solid #cccccc;
		}
	}

	&:hover {

		.phone-dropdown__list {

			visibility: visible;
			opacity: 1;
			transform: translateY(0);
		}

		.phone-dropdown__button::before {
		
			border-width: 0 5.5px 6px 5.5px;
			border-color: transparent transparent #a19c9f transparent;
		}
	}
}