/*============================================================================
	#Post Page
==============================================================================*/
.article {

	padding: 0 80px;

	&__header {
		
	}

	&__title {

		padding: 30px 0 40px;
		font-size: 36px;
		line-height: 1.27;
		color: #4f0328;
		font-weight: $regular;
	}

	&__meta {

		display: flex;
		justify-content: center;
		margin-bottom: 15px;
		font-size: 18px;

		span {

			color: #967786;
			margin: 0 8px;
		}
	}

	&__date {

		color: #967786;
	}

	&__category {

		color: #e09300;
		text-decoration: none;
		transition: color 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);

		&:hover {

			color: $colorOrange;
			text-decoration: underline;
		}
	}

	&__media {

		margin: 55px 0 55px 0;
		height: 550px;
		overflow: hidden;
	}

	&__image {

		height: 100%;
		width: 100%;
		object-fit: cover;
	}

	&__typography {

		padding-bottom: 40px;
		max-width: 730px;
		margin: 0 auto;
		color: #4f0328;

		p, div {

			margin: 10px 0 35px 0;
		}
	}
}

.featured-articles {

	&__header {

		display: flex;
		justify-content: center;
		margin-bottom: 50px;
	}

	&__title {

		color: #4f0328;
		font-size: 42px;
		font-weight: $regular;
	} 
}

.article-page {

	background-repeat: no-repeat;
	background-position: top 86px center;

	&__breadcrumb {

		margin-bottom: 10px;
	}

	.breadcrumb {

		margin-left: 20px;
	}

	&__likely {

		margin-bottom: 60px;
	}

	&__likely-inner {
	
		max-width: 730px;
    	margin: 0 auto;
		display: flex;
		justify-content: flex-end;
	}

	&__footer {

		padding: 70px 0 50px;
		background: #fefcf8;
	}
}