/*============================================================================
	#About Page
==============================================================================*/
.about-text-block {

	&__title {

		font-size: 24px;
		line-height: 30px;
		font-weight: $semiBold;
		color: $colorMaroon;
		margin-bottom: 25px;

		&._orange {

			color: $colorOrange;
		}
	}

	&__content {

		line-height: 24px;
	}
}

.about-main {

	
	background-repeat: no-repeat;
	background-position: top -86px center;

	&__header {

		display: flex;
		justify-content: flex-end;
		padding: 0px 70px 55px;
	}

	&__title {

		font-size: 40px;
		font-weight: $bold;
		color: white;
		text-transform: uppercase;
		display: flex;
		align-items: center;

		._separator {

			background: white;
			height: 5px;
			width: 80px;
			margin: 0 15px;
		}
	}

	&__body {

		display: flex;
	}

	&__col {

		flex: 0 0 50%;
		max-width: 50%;

		&._left {

			padding-right: 45px;
			color: #2b0317;
		}

		&._right{
			padding-top: 55px;
			padding-left: 45px;
			color: white;
		}
	}

	&__text-block {

		margin-bottom: 55px;

		&._secondary {

			padding-right: 78px;
		}

		&:last-child {

			margin-bottom: 0;
		}
	}

	&__footer {

		display: flex;
		justify-content: flex-end;
		padding-top: 0px;
	} 

	&__alert {

		max-width: 440px;
		text-align: center;
		margin: 0 225px;
	}

	&__alert-text {

		font-size: 19px;
		line-height: 26px;
		color: #f7f1eb;
	}

	&__alert-title {

		font-size: 24px;
		line-height: 36px;
		font-weight: $semiBold;
		color: #ffa800;
	}
}

.principles {

	background-repeat: no-repeat;
	background-position: center;

	.container {

		position: relative;
	}

	&__header {

		display: flex;
		justify-content: center;
		margin-bottom: 70px;
	}

	&__title {

		font-size: 48px;
		font-weight: $regular;
		line-height: 60px;
		color: $colorMaroon;
	}

	&__body {

		max-width: 780px;
		padding-left: 30px;
		margin: 0 auto;
	}

	&__items {

		display: flex;
		flex-wrap: wrap;
	}

	&__item {

		flex: 0 0 calc(50% - 60px);
		padding-left: 50px;
		font-size: 22px;
		font-weight: $semiBold;
		line-height: 28px;
		color: #7f003e;
		position: relative;
		margin: 0 30px;
		margin-bottom: 55px;

		&::before {

			content: '';
			position: absolute;
			height: 3px;
			width: 30px;
			background: $colorOrange;
			top: 14px;
			left: 0;

		}
	}

	&__bg-image {

		position: absolute;

		&._left {

			top: 10px;
			left: -375px;
			opacity: 0.4;
		}

		&._right {

			top: 70px;
			right: -230px;
		}
	}

	&__image {

		position: absolute;
		bottom: -80px;
		left: -130px;
	}
}

.team-card {

	background: #f7f1eb;
	height: 100%;

	&__container {

		display: flex;
		min-height: 252px;
		height: 100%;
	}

	&__media {

		margin: 0 0 0 0;
		flex: 0 0 212px;
		max-width: 212px;
		background: #f5ece3;
		height: auto;

		img {

			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__body {
		
		padding: 23px 16px 26px;
		display: flex;
		flex-direction: column;
	}

	&__header {

		margin-bottom: 20px;
	}

	&__name {

		font-size: 16px;
		font-weight: $bold;
		line-height: 18px;
		color: $colorMaroon;
		margin-bottom: 2px;
	}

	&__profession {

		font-size: 14px;
		font-weight: $regular;
		opacity: .5;
	}

	&__contacts {

		margin-top: auto;
	}

	&__messangers {

		margin-bottom: 2px;

		.messengers__icon {

			fill: #2e060b;
		}
	}

	&__phone-list {

		display: flex;
		flex-direction: column;
		margin-bottom: 7px;
	}

	&__phone {

		font-size: 16px;
		line-height: 18px;
		color: $colorMaroon;
		text-decoration: none;
		opacity: .8;
		margin-bottom: 4px;
		transition: opacity 0.2s ease-out;
		&:last-child {

			margin-bottom: 0;
		}

		&:hover {

			opacity: 1;
			text-decoration: underline;
		}
	}

	&__email-list {

		margin-bottom: 25px;
	}

	&__email {

		font-size: 16px;
		line-height: 18px;
		color: $colorMaroon;
		text-decoration: none;
		opacity: .8;
		margin-bottom: 4px;
		transition: opacity 0.2s ease-out;
		&:last-child {

			margin-bottom: 0;
		}

		&:hover {

			opacity: 1;
			text-decoration: underline;
		}
	}

	&__language {

		font-size: 16px;
		line-height: 18px;
		color: $colorMaroon;
		text-decoration: none;
		opacity: .8;
	}
}

.team {

	&__header {

		display: flex;
		justify-content: center;
		margin-bottom: 55px;
	}

	&__title {

		font-size: 48px;
		font-weight: $regular;
		line-height: 60px;
		color: $colorMaroon;
	}

	&__grid {

		display: flex;
		flex-wrap: wrap;
		justify-content: centerv;
	}

	&__col {

		flex: 0 0 490px;
		max-width: 490px;
		margin: 0 30px 40px;
	}
}

.contact-card {

	&__container {

		display: flex;
		min-height: 540px;
		background: rgba(247, 241, 235, 0.7);
	}

	&__main {

		flex: 0 0 348px;
		max-width: 348px;
		padding: 33px 50px 40px 28px;
		color: #2b0317;
		fill: #735f69;
	}

	&__map {

		flex: 1 1 100%;
		height: auto;
	}

	&__contacts,
	&__worktime {
		
		position: relative;

		&::before {

			content: '';
			position: absolute;
			height: 2px;
			width: calc(100% + 20px);
			background: #d9d1cf;
			bottom: 0;
			left: -15px;
		}
	}

	&__contacts {

		padding-bottom: 18px;
		margin-bottom: 14px;
	}

	&__worktime {

		padding-bottom: 22px;
		margin-bottom: 14px;
	}

	&__messangers-phones {

		display: flex;
		margin-bottom: 13px;
	}

	&__messangers {

		margin-right: 20px;

		.messengers__item {

			opacity: 1;

			&:hover {

				opacity: .7;
			}
		}
		
		.messengers__icon {

			fill: inherit;
		}
	}

	&__phone-list {
		display: flex;
		flex-wrap: wrap;
		// align-items: center;
	}	

	&__phone {

		color: inherit;
		text-decoration: none;
		font-size: 18px;
		line-height: 18px;
		transition: opacity 0.2s ease-out;
		display: block;
		margin-bottom: 4px;

		&:last-child {

			margin-bottom: 0;
		}

		&:hover {

			opacity: .7;
		}
	}

	&__email-list {

		margin-bottom: 19px;
	}

	&__email {
		color: inherit;
		text-decoration: none;
		font-size: 16px;
		line-height: 18px;
		transition: opacity 0.2s ease-out;
		display: block;
		margin-bottom: 4px;
		display: flex;
		align-items: center;

		&:last-child {

			margin-bottom: 0;
		}

		&:hover {

			opacity: .7;
		}

		._icon {

			flex: 0 0 auto;
			margin-top: -2px;
			width: 21px;
    		height: 21px;
			margin-right: 10px;
		}
	}

	&__address-list {

		// margin-bottom: 15px;
	}

	&__address {

		display: flex;
		margin-bottom: 4px;
		&:last-child {

			margin-bottom: 0;
		}

		._icon {

			flex: 0 0 auto;
			margin-top: 0px;
			width: 19px;
    		height: 25px;
			margin-right: 10px;
		}

		span {

			display: block;
		}
	}

	&__block-title {

		color: #2b0317;
		font-weight: $semiBold;
	}

	&__worktime-list {

		margin-top: 4px;
	}

	&__worktime-item {

		margin-bottom: 9px;
		line-height: 18px;

		span {

			font-weight: $semiBold;
			text-transform: uppercase;
		}

		&:last-child {

			margin-bottom: 0;
		}
	}

	&__socialmedia {

		margin-bottom: 43px;

		.socialmedia {

			margin-top: 16px;

			&__link {

				opacity: 1;
				color: inherit;
				margin: 0 15px 0 0;
				
				&:last-child {

					margin-right: 0;
				}

				&:hover {

					opacity: .7;
				}
			}

			&__icon {

				fill: inherit;
			}
		}
	}

	&__callback {

		.button-primary {

			border-radius: 4px;
		}
	}
}

.contacts {

	padding: 100px 0 200px;
	background-position: top center;
	background-repeat: no-repeat;
	background-color: #4f0328;

	&__header {

		padding: 0 300px;
		margin-bottom: 45px;
	}

	&__title {

		font-size: 48px;
		font-weight: $regular;
		line-height: 60px;
		color: $colorMaroon;
	}

	&__body {

		display: flex;
	}

	&__controll {

		flex: 0 0 270px;
		max-width: 270px;
		padding: 30px 15px 30px 0;
	}

	&__button {

		color: #4f0328;
		font-size: 20px;
		font-weight: $regular;
		text-transform: uppercase;
		margin-bottom: 40px;
		display: flex;
		align-items: center;
		// justify-content: space-between;
		width: 100%;
		opacity: .4;
		transition: opacity 0.2s ease-out;
		text-align: left;

		span {
			
			position: relative;
			margin-right: 30px;

			&::before {

				content: '';
				position: absolute;
				width: 0;
				height: 2px;
				background: #4f0328;
				left: 0;
				bottom: -2px;
				transition: width 0.2s ease-out;
			}
		}

		._icon {

			flex: 0 0 auto;
			width: 26px;
			height: 16px;
			fill: #4f0328;
			opacity: 0;
			transform: translateX(-10px) rotate(180deg);
			transition: transform 0.2s ease-out, opacity 0.2s ease-out;
		}

		&._active {

			opacity: 1;

			span {

				&::before {

					width: 100%;
				}
			}

			._icon {

				opacity: 1;
				transform: translateX(0) rotate(180deg);
			}
		}

		&:not(._active):hover {

			opacity: .8;

			span {

				&::before {

					width: 100%;
				}
			}
		}

	}

	&__content {

		width: 100%;
	}

	&__content-card {

		display: none;

		&._active {

			display: block;
		}
	}
}

.bank-card {

	&__header {

		margin-bottom: 23px;
	}

	&__title {

		font-size: 28px;
		line-height: 30px;
		font-weight: $semiBold;
		color: #4f0328;
	}

	&__info-list {

		margin: 0;
		padding: 0;
		list-style-type: none;
	}

	&__info-item {

		margin-bottom: 10px;
		font-size: 18px;
		color: #917580;

		span {

			font-weight: $semiBold;
			color: #4f0328;
			margin-right: 4px;
		}

		&:last-child {

			margin-bottom: 0;
		}
	}
}

.bank-info {

	&__row {

		display: flex;
		flex-wrap: wrap;
		margin: 0 -15px;
	}

	&__col {

		margin:  0 15px;
		flex: 0 0 calc(100% / 2 - 30px);
		max-width: calc(100% / 2 - 30px);;	
	}
} 		


.about-page {

	&__main {

		padding: 60px 0 56px;
	}

	&__principles {

		padding: 70px 0 45px;
	}

	&__team {

		padding: 90px 0 60px;
	}

	&__bank-info {

		padding: 95px 0 100px;
	}
}