/*============================================================================
	#Advantage
==============================================================================*/

.advantage {
	
	padding: 110px 0 105px;
	background-repeat: no-repeat;
	background-position: center bottom;

	&__container {

		position: relative;
	}

	&__header {

		margin-bottom: 40px;
	}

	&__title {

		padding: 0 175px;
		margin-bottom: 17px;
	}

	&__description {

		margin: 0 110px;
		max-width: 730px;
		color: $colorMaroon;
		font-size: 18px;
		line-height: 1.33;

		strong {
			font-weight: $semiBold;
		}
	}

	&__body {

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__card {

		flex: 0 0 365px;
		background: #f5ede6;
		padding: 30px 30px 30px 30px;
		margin-bottom: 20px;
		position: relative;
		z-index: 10;

		&:nth-child(2n+1){

			margin-right: 60px;
		}

		&:last-child {

			margin-right: 200px;
		}
	}

	&__card-title {

		font-size: 22px;
		font-weight: $semiBold;
		line-height: 26px;
		color: $colorOrange;
		margin: 2px 0 15px 18px;
	}

	&__card-text {

		margin: 0 0 5px 0;
		font-size: 16px;
		line-height: 24px;

		&::before {

			content: '';
			display: inline-block;
			margin-right: 10px;
			position: relative;
			top: -2px;
			height: 8px;
			width: 8px;
			border-radius: 100%;
			background: $colorOrange;
		}
	}

	&__image {

		position: absolute;
		z-index: 1;

		&._first {
			top: 240px;
			left: 530px;
			z-index: 9;
		}

		&._second {
			top: 510px;
			left: 440px;
			z-index: 9;
		}

		&._third {
			top: 270px;
			left: 290px;
			z-index: 11;
		}

		&._fourth {
			bottom: -250px;
			right: 30px;
			z-index: 11;
		}
	}
}