/*============================================================================
	#Hero
==============================================================================*/

.hero {

	position: relative;
	// height: calc(100vh - 86px);
	height: 740px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;

	&::before {

		content: '';
		position: absolute;
		height: 100%;
		width: 380px;
		background: $colorMaroon;
		left: 270px;
		opacity: .8;
	}

	&__container {
	
		display: flex;
		align-items: center;
		height: 100%;
	}

	&__content {

		margin-left: 270px;
		position: relative;
		z-index: 1;
		color: white;
		margin-bottom: 30px;
	}

	&__slogan {

		color: white;
		font-size: 22px;
		line-height: 1.36;
		font-weight: $regular;
		padding-left: 40px;
		letter-spacing: 0.05rem;
		margin: 0 0 20px 0;
	}

	&__title {

		font-size: 72px;
		font-weight: $extraBold;
		line-height: 78px;
		color: white;
		text-transform: uppercase;
		letter-spacing: 0.25rem;
		padding-left: 80px;
		margin: 0 0 0 0;

		span {

			text-shadow:
				-1px -1px 0 #000,  
				1px -1px 0 #000,
				-1px 1px 0 #000,
				1px 1px 0 #000;
			
			display: inline-block;
			margin-top: 10px;
			letter-spacing: 0.35rem;

			@supports((text-stroke: 2px black) or (-webkit-text-stroke: 2px black)) {
				
				color: transparent;
				-webkit-text-stroke: 2px white;
				text-stroke: 2px white;
				text-shadow: none;
				
			}
		}
			
	}
}
