/*============================================================================
	#Seo Text Section
==============================================================================*/

.section-typography {

	padding: 90px 0 95px;

	&__title {

		font-size: 24px;
		line-height: 30px;
		color: $colorMaroon;
		font-weight: $semiBold;
		margin-bottom: 35px;
	}

	&__body {

		height: 285px;
		overflow: hidden;
		transition: height 0.6s cubic-bezier(0.33, 1, 0.68, 1);
		position: relative;
		margin-bottom: 25px;
	
		&._visible {

			transition: height 0.6s cubic-bezier(0.33, 1, 0.68, 1);
			&::before {

				opacity: 0;
			}
		}

		&::before {

			content: '';
			height: 165px;
			width: 100%;
			position: absolute;
			bottom: 0;
			left: 0;
			// background: rgb(255,255,255);
			background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
			transition: opacity 0.6s cubic-bezier(0.33, 1, 0.68, 1);
		}

		.typography {

			padding-bottom: 40px;
		} 
	}

	&__footer {

		display: flex;
		justify-content: center;
	}

	&__button {

		font-weight: $semiBold;
		color: #a90957;
		transition: color 0.2s ease-out;

		&:hover {
			color: #aa0a58;
		}
	}
}