/*============================================================================
	#Tour
==============================================================================*/

.tour {

	background-repeat: no-repeat;
	background-position: top 0px center;
	padding: 140px 0 75px;

	&__container {

		position: relative;
	}

	&__body {

		display: flex;
	}

	&__content {
		flex: 0 0 530px;
		max-width: 530px;
		margin-left: 115px;
		margin-right: 60px;
	}

	&__title {
		margin-bottom: 33px;
	}

	&__typography {

		margin-bottom: 60px;
	}

	&__media {

		margin: 0;
		overflow: hidden;
		border-radius: 300px;
		margin-top: 20px;
	}

	&__footer {

		display: flex;
		justify-content: center;
	}

	&__background {

		._image-left {

			position: absolute;
			top: -10px;
			left: -380px;
		}

		._image-right {

			position: absolute;
			top: -50px;
			right: -390px;
		}

		@media (max-width: 1440px) {

			display: none;
		}
	}
}