/*============================================================================
	#Testimonials
==============================================================================*/
.testimonials-slider {

	&__nav {

		display: flex;
		align-items: center;
		margin-top: 30px;
		padding: 0 125px;
	}
}

.testimonials-slide {

	display: flex;
	background: #fefcf8;

	&__media {

		flex: 0 0 445px;
		width: 445px;
		height: 345px;
		box-sizing: border-box;
		margin: 0 0 0 0;
		background: #f0e2d6;
		position: relative;
		border: 24px solid #f5ede6; 
		overflow: hidden;
		border-radius: 200px;
		margin-right: 50px;	
	}

	&__video {
		flex: 0 0 445px;
		width: 445px;
		height: 345px;
		margin: 0 0 0 0;
		margin-right: 50px;	

		.yt-player {

			padding-top: 345px;
		}
	}

	&__image {

		height: 100%;
		width: 100%;
		object-fit: cover;
	}

	&__body {

		padding-top: 30px;
		max-width: 450px;
	}

	&__header {

		display: flex;
		justify-content: space-between;
		font-size: 15px;
		margin-bottom: 22px;
	}

	&__user-name {

		font-size: 19px;
		font-weight: $semiBold;
		line-height: 1.4;
		color: $colorMaroon;
	}

	&__user-location,
	&__date {

		opacity: 0.7;
		letter-spacing: 0.025rem;
	}

	&__date {

		margin-left: 20px;
		flex: 0 0 80px;
		text-align: right;
		margin-top: 4px;
	} 

	&__typography {

		overflow: hidden;
		transition: height 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
	}
	
}

.testimonials {

	background-color: #4f0328;
	padding-bottom: 50px;

	&__inner {

		position: relative;
		z-index: 1;
		padding: 85px 0 70px;

		&::before {

			content: '';
			position: absolute;
			background: #fefcf8;
			width: 1920px;
			height: 100%;
			top: 0;
			left: -120px;
			z-index: -1;

		}

		&::after {

			content: '';
			position: absolute;
			bottom: -0px;
			right: -400px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 0 0 310px 310px;
			border-color: transparent transparent #4f0328 transparent;
		} 
	}

	&__header {

		padding: 0 175px;
		margin-bottom: 35px;
	}

	&__body {

		padding: 0 55px;
	}

	.swiper-slide {

		background: #fefcf8;
		height: auto;
	}
}