/*============================================================================
	#Catalog Tabs
==============================================================================*/
.catalog-tabs {

	padding: 75px 0 100px;

	&__controll {

		display: flex;
		justify-content: space-around;
		margin-bottom: 55px;
	}

	&__body {

		// margin-bottom: 30px;
	}

	&__button-tab {

		padding: 10px 10px;
		font-size: 24px;
		line-height: 1;
		text-transform: uppercase;
		position: relative;
		color: #beb8bb;
		transition: color .2s ease-out;
		
		&::after {

			content: '';
			position: absolute;
			height: 2px;
			width: 0;
			background: #beb8bb;
			left: 0;
			bottom: 0;
			opacity: 1;
			transition: width .2s ease-out;
		}

		&._current {

			color: $colorFont;

			&::after {

				width: 100%;
				background: $colorFont;
			}
		}

		&:hover {

			&::after {

				width: 100%;
			}
		} 
	}

	&__content {

		display: none;

		&._current {

			display: block;
		}
	}

	&__footer {

		display: flex;
		justify-content: center;
		margin-top: 30px;
	}
}
