/*============================================================================
	#Testimonial Page
==============================================================================*/
.testimonial-list {

	position: relative;
	z-index: 1;
}

.testimonial-card {

	display: flex;
	margin-bottom: 160px;
	position: relative;

	&:last-child {

		margin-bottom: 100px;
	}

	&__media {

		flex: 0 0 calc(100% / 2);
		width: calc(100% / 2);
		box-sizing: border-box;
		position: relative;

		.yt-player {

			padding-top: 305px;
			margin: 20px 10px 0 25px;
			position: relative;

			&::before {

				content: '';
				position: absolute;
				width: 820px;
				height: 860px;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				z-index: -1;
				background: url('../images/testimonials/video-bg.jpg') no-repeat;
				background-position: center center;
			}
		}
	}

	&__figure {

		background: #e4dcd5;
		border: 26px solid #f5ede6; 
		overflow: hidden;
		border-radius: 200px;
		margin: 0 55px 0 70px;
		height: 345px;
	}

	&__image {

		height: 100%;
		width: 100%;
		object-fit: cover;
	}

	&__body {

		padding-top: 30px;
		flex: 0 0 calc(100% / 2);
		max-width: calc(100% / 2);
		padding-left: 25px;
	}

	&__header {

		display: flex;
		justify-content: space-between;
		font-size: 15px;
		margin-bottom: 18px;
	}

	&__user-name {

		font-size: 19px;
		font-weight: $semiBold;
		line-height: 1.4;
		color: $colorMaroon;
	}

	&__user-location,
	&__date {

		opacity: 1;
		letter-spacing: 0.025rem;
		color: #81757a;
		
	}

	&__date {

		margin-left: 20px;
		flex: 0 0 80px;
		text-align: right;
		margin-top: 4px;
	} 

	&__typography {

		overflow: hidden;
		transition: height 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);

		p {

			margin-bottom: 30px;
		}
	}

	&__readmore {

		min-width: 120px;
		display: block;
		margin: 15px auto 0;
		color: #a90957;
		font-weight: $semiBold;
		text-decoration: underline;

		&:hover {

			text-decoration: none;
		}
	}
	
}

.testimonials-page {

	padding-bottom: 80px;
	background-repeat: no-repeat;
	background-position: top 70px center;

	&__header {

		padding-top: 40px;
		padding-bottom: 35px;

		.container {

			position: relative;
			display: flex;
			justify-content: space-between;
		}
	}

	&__title {

		color: white;
		font-size: 40px;
		line-height: 65px;
		text-transform: uppercase;
		font-weight: $bold;
		margin: 30px 0 0;
		margin-left: 190px;

		span {
			display: block;
			letter-spacing: 0.035em;
		}
	}
}