/*============================================================================
	#Filter
==============================================================================*/
.filter {

	position: relative;
	z-index: 20;

	&._home-page {
		margin-top: -150px;
	}

	.container {

		position: relative;

		&::before {

			content: '';
			position: absolute;
			width: 1920px;
			height: 100%;
			background: white;
			border-top-right-radius: 150px;
			right: -145px;
		}
	}



	&__form {

		position: relative;
		padding: 45px 0 50px;
		z-index: 1;
	}

	&__header {

		display: flex;
		justify-content: flex-end;
		margin-bottom: 52px;
	}

	&__button-type {

		font-size: 42px;
		font-weight: $semiBold;
		line-height: 1;
		letter-spacing: 0.05rem;
		padding-bottom: 8px;
		position: relative;
		text-transform: uppercase;

		margin-left: 100px;
		

		&::before {

			content: '';
			position: absolute;
			height: 2px;
			width: 100%;
			background: $colorOrange;
			opacity: 0;
			bottom: 0;
			transform: translateY(-8px);
			transition: all .2s ease-out;
		}

		&::after {

			content: '';
			position: absolute;
			top: calc(50% - 14px);
			left: -36px;
			width: 26px;
			height: 21px;
			background: url('../icons/arrows-right.svg') no-repeat center;
			background-clip: 100%;
			opacity: 0;
			transform: translateX(-10px);
			transition: all .2s ease-out;
		}

		&._active {

			&::after {
				opacity: 1;
				transform: translateX(0);
			}

			&::before {

				opacity: 1;
				transform: translateY(0);
			}
		}

		&:hover {

			&::before {

				opacity: 1;
				transform: translateY(0);
			}
		}

		
	}

	&__row {

		display: flex;
		align-items: center;
		margin-bottom: 30px;
	}

	&__checkbox-area {

		display: flex;
		align-items: center;

		.checkbox {

			margin-right: 50px;
		}
	}

	&__reset {

		margin-left: auto;
		font-size: 16px;
		font-weight: $semiBold;
		color: #a19c9f;
		fill: #a19c9f;
		letter-spacing: 0.025rem;
		text-transform: uppercase;
		display: flex;
		align-items: center;
		transition: color .2s ease-out;

		&._disabled {

			.icon {

				opacity: 0;
			}
		}

		.icon {
		
			width: 16px;
			height: 16px;
			fill: inherit;
			margin-left: 13px;
			transition: fill .2s ease-out, opacity .2s ease-out;
		}

		&:hover {

			fill: #807379;
			color: #807379;
		}
	}

	&__group {

		display: flex;
		flex-direction: column;
		margin-right: 20px;

		&:last-child {
			margin-right: 0;
		}

		&._category {
			
			flex: 1 0 265px;
			max-width: 265px;
		}

		&._location {
			
			flex: 1 0 190px;
			max-width: 190px;
		}

		&._type {
			
			flex: 1 0 175px;
			max-width: 175px;
		}

		&._price {

			flex: 1 0 255px;
			max-width: 255px;
		}
	}

	&__group-title {

		font-size: 16px;
		font-weight: $bold;
		text-transform: uppercase;
		margin-bottom: 10px;
		padding-left: 6px;
	}

	&__input {

		width: 100%;
		font-size: 14px;
		line-height: 1.4;
		border: 1px solid #b99aa9;
		border-radius: 40px;
		padding: 13px 34px 14px 22px;

		&:focus {

			border-color: #2c0417;
		}
	}

	&__price-group {

		display: flex;
		align-items: baseline;

		.input {
			font-size: 14px;
			line-height: 1.4;
			border: 1px solid #b99aa9;
			padding: 8px 12px;
			width: 100%;
			border-radius: 4px;

			&:focus {

				border-color: #2c0417;
			}
		}

		span {
			margin-left: 8px;
		}
	}

	&__footer {

		// background: #ccc;
		display: flex;
		justify-content: flex-end;
		margin-top: -10px;
	}

	&__submit {

		background: #7f003e;
		font-size: 14px;
		font-weight: $semiBold;
		text-transform: uppercase;
		color: white;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 15px 26px 15px 22px;
		transition: background .2s ease-out;

		.icon {

			fill: white;
			width: 34px;
			height: 26px;
			margin-right: 16px;
		}

		span {

			color: #ffc200;
			display: inline-block;
			margin: 0 5px;
		}

		&:hover {

			background: #4f0327;
		}
	}
}