/*============================================================================
	#Header
==============================================================================*/
.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 100;
	background: white;

	&__container {

		display: flex;
		height: 86px;
	}

	&__logo {

		background: $colorMaroon;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-grow: 1;
		max-width: 270px;
	}

	&__main {
		flex-grow: 1;
		display: flex;
		flex-direction: column;
	}

	&__contacts {

		display: flex;
		justify-content: flex-end;
		align-items: center;
		padding: 0px 30px 0;
	}

	&__contacts-item {

		&._messengers {

			margin-right: 6px;
		}

		&._phones {

			margin-right: 32px;
		}
	}

	&__email {

		display: flex;
		align-items: center;
		color: #787577;
		text-decoration: none;
		font-size: 16px;

		&:hover {

			color: #2c0417;
		}

		img {
			margin-top: 3px;
			margin-right: 10px;
			font-size: 16px;
		}
	}

	&__nav {

		padding: 0 20px;
		margin-top: auto;
	}

	&__currency {

		border-left: 1px solid #b8b8b8;
		border-bottom: 1px solid #b8b8b8;
		width: 86px;
	}

}

.main-nav {

	&__container {

		display: flex;
	}

	&__link {

		display: block;
		font-size: 15px;
		font-weight: $semiBold;
		color: #4f4c4d;
		text-decoration: none;
		text-transform: uppercase;
		padding: 15px 20px;
		letter-spacing: 0.01rem;
		position: relative;
		transition: color .2s ease-out;


		&:hover {

			color: $colorOrange;
		}

		&._active {

			color: $colorOrange;
			font-weight: $bold;
		}
	}

}
