/*============================================================================
	#Blog
==============================================================================*/
.blog-categories {

	border-top: 4px solid #f8e5c1;

	.container {

		position: relative;
	}

	&__slider {

		padding: 0 50px;
		margin-top: -4px;
	}

	&__item {

		font-size: 18px;
		font-weight: $semiBold;
		color: #4f0328;
		height: 76px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		width: 100%;
		opacity: .6;
		transition: opacity 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);

		&::before {

			content: '';
			position: absolute;
			top: 0;
			left: 0;
			height: 4px;
			width: 100%;
			background: $colorOrange;
			opacity: 0;
			transition: opacity 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
		}

		&._active {

			
			opacity: 1;
			
			
			&::before {

				opacity: 1;
			}
		}
	}

	&__button {

		width: 48px;
		height: 48px;
		border-radius: 100%;
		border: 2px solid #ffb423;
		border-right-color: transparent;
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 50%;
		transform: translateY(calc(-50% + 2px));

		._icon {

			fill: #ffb423;
			width: 26px;
			height: 16px;
			margin-right: -25px;
			transition: transform 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
		}

		&._next {
		
			right: 15px;
			transform: translateY(calc(-50% + 2px)) rotate(180deg) ;
		}

		&:hover {

			._icon {

				transform: translateX(-10px);
			}
		}
	}

}

.blog-categories__slider._disabled {

	padding: 0;

	.swiper-slide {

		width: auto;
	}

	.swiper-wrapper {

		justify-content: center;
	}

	.blog-categories__item {

		padding: 0 30px;
	}
}

.blog-card {

	padding: 0 0 10px 0;

	&__link {

		text-decoration: none;
	}

	&__category {

		font-size: 16px;
		color: #e09300;
		text-align: center;
		line-height: 24px;
		margin-bottom: 12px;
	}

	&__media {

		padding-top: 100%;
		position: relative;
		margin: 0 0 10px 0;
		overflow: hidden;
		background: #f5ede6;


		&::before {

			content: '';
			width: 100%;
			height: 100%;
			background: $colorMaroon;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			opacity: 0;
			transition: opacity 0.6s cubic-bezier(0.5, 1, 0.89, 1);
		}
	}

	&__image {

		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		transform: scale3d(1, 1, 1);
		transition: all 0.6s cubic-bezier(0.5, 1, 0.89, 1);
	}

	._icon {

		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, 0);
		opacity: 0;
		fill: white;
		z-index: 2;
		width: 44px;
		height: 44px;

		transition: all 0.6s cubic-bezier(0.5, 1, 0.89, 1);
	}

	&__date {

		color: #967786;
		text-align: center;
		// padding: 10px 0;
	}

	&__title {

		font-size: 18px;
		line-height: 24px;
		font-weight: $semiBold;
		color: #4f0328;
		padding: 0 15px;
		-webkit-box-orient: vertical;
		display: block;
		display: -webkit-box;
		text-overflow: ellipsis;
		overflow: hidden;
		-webkit-line-clamp: 2;
		padding-top: 10px;
	}

	&:hover {

		.blog-card__media {
		
			&::before {

				opacity: 0.8;
			}
		}

		.blog-card__image {

			transform: scale3d(1.1, 1.1, 1.1);
		}

		.blog-card__title {

			// text-decoration: underline;
		}

		._icon {

			opacity: 1;
			transform: translate(-50%, -50%);
		}
	}
	
}

.blog-grid {

	&__container {

		margin: 0 -45px;
		display: flex;
		flex-wrap: wrap;
	}

	&__col {

		margin: 0 45px 40px;
		flex: 0 0 calc(100% / 3 - 90px);
		max-width: calc(100% / 3 - 90px);

		&:nth-child(2) .blog-card__image,
		&:nth-child(11) .blog-card__image {

			top: 40px;
			left: 40px;
			width: calc(100% - 40px * 2);
			height: calc(100% - 40px * 2);
		}
	}
}

.blog-page {

	padding-bottom: 70px;

	&__header {

		display: flex;
		justify-content: center;
		align-items: center;
		height: 153px;
		background: url('../images/blog/blog-header-bg.jpg') no-repeat;
		background-position: center;
		margin-bottom: 60px;
	}

	&__title {

		color: white;
		font-size: 36px;
		font-weight: $semiBold;
		text-transform: uppercase;
	}

	&__categories {

		margin-bottom: 40px;
	}

	&__body {

		padding: 20px 0 10px;
	}
}