/*============================================================================
	#Aside
==============================================================================*/

.aside {

	position: fixed;
	right: 0;
	bottom: 0;
	width: 86px;
	height: 100vh;
	background: white;
	z-index: 99;

	&__container {

		display: flex;
		align-items: center;
		height: 100%;
		width: 100%;
		writing-mode: vertical-lr;
		transform: scale(-1);
		padding: 50px 0;
	}

	&__button {

		display: flex;
		flex-shrink: 0;
		align-items: center;
		color: #2b0317;
		margin-bottom: 50px;

		.icon {

			margin-bottom: 16px;
			transition: transform .2s linear;
		}

		&._callback {

			.icon {

				display: flex;
				justify-content: center;
				align-items: center;
				width: 62px;
				height: 62px;
				background: $colorMaroon;
				border-radius: 100%;		
			}

			.svg {

				fill: white;
				height: 32px;
				width: 32px;
				transform: scale(-1,-1);
			}

			&:hover {

				text-decoration: underline;

				.icon {

					transform: scale(1.1);
				}
			}
		}

		&._favorits {

			color: #787577;

			.icon {

				width: 32px;
				height: 32px;
				background: #f2eff1;
				display: flex;
				justify-content: center;
				align-items: center;
				border-radius: 100%;
				font-size: 18px;
				color: #2e060b;
				transform: rotate(90deg);
				
			}

			&:hover {

				.icon {

					transform: scale(1.1) rotate(90deg) ;
				}

				._text {

					text-decoration: underline;
				}
			} 

		}

	}
}