/*============================================================================
	#Privacy Police
==============================================================================*/
.privacy-page {

	background-repeat: no-repeat;
	background-position: top 86px center;
	
	&__breadcrumbs {

		.breadcrumb {

			margin-left: 20px;
		}
	}

	&__body {

		padding: 40px 0 70px;

		.container {

			max-width: 750px;
		}
	}

	&__title {

		font-size: 36px;
		line-height: 1.2;
		font-weight: $semiBold;
		text-align: center;
		text-transform: uppercase;
		color: #4f0328;
		margin-bottom: 60px;
	}

	&__typography {

		color: #4f0328;

		p, div {

			margin: 10px 0 35px 0;
		}
	}
}