/*============================================================================
	#Page Options
==============================================================================*/
.sort-select {

	font-size: 15px;
	line-height: 1;
	color: #b6859d;
	text-transform: lowercase;
	border: 1px solid #a56987;
	border-radius: 4px;
	display: inline-block;
	
	cursor: pointer;
	position: relative;
	background: white;
	z-index: 10;
	min-width: 210px;

	&._active {

		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;

		&::before {

			transform: translateY(-45%) rotate(180deg);
		}
	} 

	&::before {

		content: '';
		position: absolute;
		width: 10px;
		height: 5px;
		top: 50%;
		right: 10px;
		background: url('../icons/arrow-angel-rose.svg');
		background-repeat: no-repeat;
		background-size: contain;
		transform: translateY(-45%);
	}

	&__current {
		display: block;
		padding: 7px 30px 8px 10px;
	}

	&__list {
		display: none;
		position: absolute;
		background: white;
		margin: 0;
		padding: 0;
		list-style-type: none;
		border: 1px solid #a56987;
		width: calc(100% + 2px);
		left: -1px;
		// bottom: 0;
		// transform: translateY(100%);
		
	}

	&__item {

		padding: 6px 10px 8px 10px;

		&:hover {

			background: rgba(165, 105, 135, 0.1);
			color: $colorFont;
		}
	}
}

.page-total-items {

	&__value {
		
		color: #b6859d;
	}
}

.page-options {

	&__inner {

		display: flex;
		padding: 10px 30px;
		align-items: center;
	}

	&__item {

		margin-right: 32px;
		display: flex;
		align-items: center;
	}

	&__item-title {

		margin-right: 10px;
	}
}
