/*============================================================================
	#Currency Dropdown
==============================================================================*/

.currency-dropdown {

	position: relative;
	height: 100%;

	&__button {

		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 18px;
		color: #787577;
		padding-left: 15px;
		padding-right: 15px;
		transition: color .2s linear;

		&::before {

			content: '';
			position: absolute;
			top: 50%;
			right: 0;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 6px 5.5px 0 5.5px;
			border-color: #a19c9f transparent transparent transparent;
			transform: translate( -15px, -50%);
		}

		&._active {
			
			&::before {
			
				border-width: 0 5.5px 6px 5.5px;
				border-color: transparent transparent #a19c9f transparent;
			}
		}

		&:hover {

			color: #2c0417;
		}
	}

	&__list {

		position: absolute;
		// display: none;
		background: #f5ede6;
		width: calc( 100% + 1px );
		transform: translateY(1px);
		border-left: 1px solid #b8b8b8;
		margin-left: -1px;
		transform: translateY(10px);
		opacity: 0;
		visibility: hidden;
		transition: all 0.2s linear;
	}

	&__item {

		display: block;
		padding: 12px 0 12px 15px;
		border-bottom: 1px solid #b8b8b8;
		width: 100%;
		text-align: left;
		// color: #2c0417;
		color: #a59896;
		transition: color .3s linear;

		&:hover {

			color: #2c0417;
		}
	}

	&:hover {

		.currency-dropdown__list {

			opacity: 1;
			visibility: visible;
			transform: translateY(0);
		}

		.currency-dropdown__button::before {

			border-width: 0 5.5px 6px 5.5px;
			border-color: transparent transparent #a19c9f transparent;
		}
	}
}