/*============================================================================
	#Product Gallery
==============================================================================*/

.product-gallery {

	margin-left: 40px;
	height: 540px;
	position: relative;

	&__slide {

		height: 540px;
		display: block;
		overflow: hidden;
	}

	&__image {

		height: 100%;
		width: 100%;
		object-fit: cover;
	}

	&__badge {

		position: absolute;
		top: 0;
		right: 0;
		z-index: 11;
		font-size: 16px;
		font-weight: $bold;
		color: #2b0317;
		background: #e6d9d7;
		border-bottom-left-radius: 22px;
		padding: 10px 30px 12px 25px;
		display: flex;
		align-items: center;
		cursor: pointer;
		transition: background 0.2s ease-out;
		// pointer-events: none;

		&:hover {

			background: rgba(230, 217, 215, 0.85);

			.icon-option__circle {
			
				fill: $colorOrange;
			}
		}

		.icon-option {
			width: 21px;
			height: 21px;
			margin-right: 14px;
		}
		
		.icon-option__circle {
			
			fill: #7F003E;
			transition: fill 0.2s ease-out;
		}

		._total {

			display: inline-block;
			margin: 0 5px;
		}
	}

	&__navigation-item {

		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 0;
		height: 100%;
		width: 70px;
		transition: background 0.3s ease-out;
		background: rgba(230, 217, 215, 0.2);
		z-index: 1;

		&:not(._disabled):hover {
			background: rgba(230, 217, 215, 0.5);
			cursor: pointer;

			.swiper-button ._icon {

				transform: translateX(3px);
				fill: #4f0328;
				
			}
		}

		&._disabled {

			cursor: default;

			.swiper-button {

				opacity: 0.7;
				cursor: default;
			}

			.swiper-button ._icon {

				transform: translateX(0px);
				fill: $colorOrange;
				
			}
			
		}

		&._prev {

			left: 0;
		}

		&._next {

			right: 0;
		}

		.button {

			background: #f7f0ea;
		}
	}

	&__pagination {

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 0;
		left: -30px;
		z-index: 2;
		height: 100%;
		width: auto;

		._current {

			font-weight: $semiBold;
			color: $colorMaroon;
		}

		._totla {

			font-weight: $semiBold;
			color: #2b0317;
			opacity: .7;
		}

		._separator {

			width: 2px;
			height: 155px;
			background: #4f0328;
			display: block;
			margin: 15px 0;
		}
	}


}