/*============================================================================
	#Category Slider
==============================================================================*/

.category-slider {

	position: relative;

	&__nav {
		position: absolute;
		top: 40px;
		right: 100px;
		display: flex;
		align-items: center;
		z-index: 1;
	}

	&__counter {

		margin: 0 32px;
		color: $colorMaroon;
		opacity: .8;
		font-size: 24px;

	}
}

.category-slide {

	height: 460px;
	display: flex;
	

	&__content {
		padding: 80px 50px 40px 50px;
		background: #f5ede6;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		box-sizing: border-box;
		flex: 0 0 380px;
	}

	&__title {

		color: $colorMaroon;
		font-size: 30px;
		font-weight: $regular;
		line-height: 1.13;
	}

	&__link {

		display: block;
		font-style: 16px;
		color: $colorMaroon;
		text-decoration: none;
		margin-left: auto;
		display: flex;
		align-items: center;

		.icon {
			margin-top: 2px;
			fill: $colorMaroon;
			height: 12px;
			width: 18px;
			margin-left: 6px;
			transition: transform .2s ease-out;
		}

		&:hover {

			.icon {

				transform: translateX(10px);
			}
		}
	}

	&__media {

		display: flex;
		align-items: flex-end;
	}

	&__thumb {

		margin: 0;
		display: flex;
		flex-shrink: 0;
		height: 100%;
		position: relative;
		
		&:not(:last-child){

			margin-right: 60px;
		}
	}

	&__image {

		width: 380px;
		height: 100%;
		object-fit: cover;
	}

	&__secondary-image {

		width: 245px;
		height: 296px;
		object-fit: cover;
		margin-top: auto;
		margin-bottom: 40px;
		// opacity: .6;
	}
}