/*============================================================================
	#Product Page
==============================================================================*/

.product-page {

	padding-bottom: 70px;

	background-repeat: no-repeat;
	background-position: top 86px left;

	&__heading {

		padding: 20px 30px;
		min-height: 120px;
		margin-bottom: 16px;
		position: relative;
		z-index: 1;

		&::before {

			content: '';
			position: absolute;
			z-index: -1;
			top: 0;
			left: 0;
			width: 1920px;
			height: 100%;
			background: rgba(247, 241, 235, .8);
		}
	}

	&__title {

		color: $colorMaroon;
		font-size: 30px;
		line-height: 38px;
		font-weight: $regular;
	}

	&__body {

		padding-bottom: 90px;

		.container {

			display: flex;
		}

	}

	&__content {

		flex: 0 0 calc(100% / 12 * 8.3 - 30px);
		max-width: calc(100% / 12 * 8.3 - 30px);
		margin-right: 30px;
	}

	&__aside {

		flex: 0 0 calc(100% / 12 * 3.7);
	}

	&__gallery {

		margin-bottom: 40px;
	}

	&__section {

		padding: 0 10px;
		margin-bottom: 55px;
	}

	&__section-head {

		margin-bottom: 20px;
	}

	&__section-title {

		font-size: 26px;
		font-weight: $regular;
		color: #2b0317;
	}

	&__section-body {

		color: rgba(79, 3, 40, 0.7);
	}	

}

.product-aside {

	&__product-info {

		margin-bottom: 40px;
	}
}

.product-info {

	&__main {

		padding: 15px 20px 18px 20px;
		background: rgba(245, 237, 230, 0.6);
		margin-bottom: 14px;
	}

	&__price {

		text-align: center;
		font-size: 30px;
		font-weight: $bold;
		line-height: 1;
		color: #4f0328;
		letter-spacing: 0.01rem;
		padding-bottom: 17px;
	}

	&__wishlist {

		padding: 8px 0px 8px 0px;
		border-top: 1px solid rgba(79, 3, 40, 0.23);
		border-bottom: 1px solid rgba(79, 3, 40, 0.23);
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__likely {

		display: flex;
		align-items: center;

		.likely .likely__widget {

			margin: 0 5px 0 0;
			height: 26px;

			&:last-child {

				margin-right: 0;
			}
			
		}

		.likely-big .likely__icon {
			width: 26px;
			height: 26px;
		}

		.likely-big svg {
			left: 4px;
			top: 4px;
			width: 18px;
			height: 18px;
		}
	}

	&__add-wishlist {

		color: rgba(43, 3, 23, 0.9);
		font-size: 13px;
		display: flex;
		align-items: center;

		._icon {

			width: 15px;
			height: 20px;
			fill: #cfcdce;
			transition: fill 0.2s ease-out;
			margin-right: 10px;
		}

		&._active {

			._icon {

				fill: $colorOrange;
			}

			
		}

		&:hover {

			text-decoration: underline;
		}
	}

	&__description-list {

		padding: 9px 10px 0px;
		margin: 0 ;
	}

	&__description-title,
	&__description-item {

		display: inline;
		margin: 0;
		line-height: 32px;
	}

	&__description-title {

		color: rgba(43, 3, 23, 0.7);
		color: #7e6f73;
		margin-right: 3px;
	}

	&__description-item {
		color: #2b0317;

		&::after { 
			content: '\A';
			white-space: pre;
		}
	}

	&__button-primary {

		font-size: 14px;
		line-height: 18px;
		width: 100%;
		padding: 10px;
		border-radius: 4px;
	}
}

.experts-aside {

	&__header {

		height: 70px;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		background: #f7f1eb;
		margin-bottom: 10px;
	}

	&__title {

		font-size: 24px;
		font-weight: $regular;
		color: $colorMaroon;
	}
}

.expert-card {

	margin-bottom: 32px;

	&__body {

		display: flex;
		background: #f7f1eb;
	}

	&__media {

		margin: 0 ;
		max-width: 130px;

	}

	&__image {

		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__content {

		padding: 20px 15px 14px;
	}

	&__name {

		font-size: 16px;
		line-height: 22px;
		font-weight: $bold;
		color: $colorMaroon;
		margin-bottom: 4px;
	}

	&__status {

		font-size: 14px;
		line-height: 20px;
		color: $colorMaroon;
		opacity: .5;
		margin-bottom: 25px;
	}

	.messengers__icon {

		fill: #2e060b;
		opacity: 1;
	}

	&__phone {

		color: $colorMaroon;
		opacity: .6;
		text-decoration: none;
		transition: opacity 0.1s ease-out;
		display: block;
		line-height: 22px;

		&:hover {

			opacity: 1;
		}
	}

	&__footer {

		padding: 4px 6px;
	}

	&__email,
	&__language {

		font-size: 15px;
		line-height: 18px;
		color: $colorMaroon;
		opacity: 0.78;
		text-decoration: none;
		transition: opacity 0.1s ease-out;

		&:hover {

			opacity: 1;
		}
	}

	&__language {
	
		&:hover {

			opacity: 0.78;
		}
	}
	
}

.featured-products {

	&__header {

		display: flex;
		justify-content: center;
		margin-bottom: 30px;
	}

	&__title {

		font-size: 36px;
		font-weight: $regular;
		color: #7f003e;
	}
}

.product-options {

	&__list {

		display: flex;
		flex-wrap: wrap;
		margin: 0 -10px;
	}

	&__item {

		flex: 0 0 calc(100% / 2 - 20px);
		margin: 4px 10px;
		font-size: 18px;
		padding-left: 22px;
		position: relative;

		&::before {

			content: '';
			position: absolute;
			top: 8px;
			left: 2px;
			height: 8px;
			width: 8px;
			background: $colorOrange;
			border-radius: 50%;
		}
	}
}

.product-location {

	height: 280px;
	width: calc(100% + 20px);
	margin-left: -10px;
}

.credit {

	position: relative;
	// z-index: 1;

	&::before{

		content: '';
		position: absolute;
		width: 985px;
		height: 835px;
		background: url('../images/product/credit-bg.jpg');
		background-repeat: no-repeat;
		background-position: center;
		z-index: -1;
		bottom: -260px;
		right: 0;
		pointer-events: none;
	}

	&__content {

		display: flex;
		margin: 0 -25px;
		margin-bottom: 10px;
	}

	&__content-item {

		flex: 0 0 calc(100% / 2 - 50px);
		margin: 0 25px;

		&._right {
	
			margin-top: -45px;
		}
	}

}

.product-feedback {

	&__title {

		color: #2b0317;
		font-size: 26px;
		font-weight: $bold;
		margin-bottom: 25px;
	}
}

.product-form {

	&__body {

		display: flex;
	}

	&__content {

		flex: 0 0 275px;
		max-width: 275px;
		margin-right: 20px;
	}

	&__tags-title {

		font-size: 15px;
		color: #4f0328;
		margin-bottom: 15px;
	}

	&__tags-list {

		display: flex;
		flex-wrap: wrap;
		
	}

	&__tag {

		background: #f5ede6;
		padding: 2px 16px 4px;
		border-radius: 4px;
		margin-right: 10px;
		margin-bottom: 10px;
		transition: all 0.2s ease-out;

		span {

			font-size: 14px;
			line-height: 18px;
			color: rgba(79, 3, 40, 0.58) ;
			transition: all 0.2s ease-out;
		}

		&._checked {

			background: $colorOrange;
			transition: all 0.2s ease-out;

			span {

				color: $colorMaroon;
				transition: all 0.2s ease-out;
			}
		}

		input[type="checkbox"]{

			position: absolute;
			z-index: -1;
			opacity: 0;
			visibility: hidden;
		}

	}

	.button-primary {

		width: 100%;
		font-size: 16px;
		padding: 7px 30px 9px;
		border-radius: 4px;
	}
}