/*============================================================================
	#Product Card
==============================================================================*/
.product-card {
	
	// width: 365px; // Удалить !!!

	background: #fbfcfd;

	&:hover {

		.product-card__image {

			// transform: scale3d(1.2, 1.2, 1.2);
			transform: scale3d(1,1,1);
		}
	}

	.product-card__link-wrapper {

		text-decoration: none;
		color: $colorFont;
	}

	&__header {

		height: 380px;
		position: relative;
		overflow: hidden;
	}

	&__thumb {

		margin: 0;
		padding: 0;
		height: 100%;
		width: 100%;
		background: #f5d1b1;
		overflow: hidden;
	}

	&__image {

		height: 100%;
		width: 100%;
		object-fit: cover;
		// transform: scale3d(1,1,1);
		transform: scale3d(1.1, 1.1, 1.1);
		transition: transform 0.6s cubic-bezier(0.33, 1, 0.68, 1);
	}

	&__badge {

		background: #7f003e;
		position: absolute;
		top: 0;
		z-index: 1;
		padding: 14px 8px 14px 20px;
		margin-right: 75px;

		// &::after {

		// 	content: '';
		// 	position: absolute;
		// 	top: 0;
		// 	right: -44px;
		// 	width: 0;
		// 	height: 0;
		// 	border-style: solid;
		// 	border-width: 45px 45px 0 0;
		// 	border-color: #7f003e transparent transparent transparent;
		// }

		&::before {

			content: '';
			position: absolute;
			top: 0;
			right: -12%;
			width: 100%;
			height: 100%;
			background-color: #7f003e;
			transform: skewX(-44deg);
			z-index: -1;
		}

		span {

			color: white;
			text-transform: uppercase;
			font-size: 16px;
			font-weight: $semiBold;
			line-height: 22px;
			letter-spacing: 0.01rem;
			display: block;
			-webkit-box-orient: vertical;
			display: block;
			display: -webkit-box;
			text-overflow: ellipsis;
			overflow: hidden;
			-webkit-line-clamp: 2;
		}
	}

	&__wishlist {

		position: absolute;
		bottom: 0;
		right: 0;
		z-index: 1;

		width: 90px;
		height: 90px;

		display: flex;
		justify-content: flex-end;
		align-items: flex-end;

		&::after {

			content: '';
			position: absolute;
			bottom: 0;
			right: 0;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 0 0 90px 90px;
			border-color: transparent transparent #fbfcfd transparent;
			z-index: -1;
		}

	}

	&__add-wishlist {
		padding: 12px 18px;

		._icon {

			width: 15px;
			height: 20px;
			fill: #ccc4c8;
			transition: fill 0.2s ease-out;
		}

		&._active {

			._icon {

				fill: $colorOrange;
				transition: fill 0.2s ease-out;
			}
		}
	}

	&__body {

		padding: 16px 6px;
		color: #807379;
	}

	&__content-top {

		display: flex;
		justify-content: space-between;
		margin-bottom: 16px;
	}

	&__price {

		font-size: 24px;
		font-weight: $bold;
		line-height: 1;
		color: $colorOrange;
		letter-spacing: 0.01rem;

		span {

			color: $colorMaroon;
			font-weight: $semiBold;
		}
	}

	&__id {

		flex: 0 0 110px;
	}

	&__content {

		display: flex;
	}

	&__title {

		font-size: 16px;
		font-weight: $regular;
		line-height: 22px;
		color: inherit;
		flex: 0 0 200px;
		margin-right: 10px;
		height: 88px;
		-webkit-box-orient: vertical;
		display: block;
		display: -webkit-box;
		text-overflow: ellipsis;
		overflow: hidden;
		-webkit-line-clamp: 4;

	}

	&__options {

		margin: 0;
		padding: 0;
		list-style-type: none;
	}

	&__option {

		display: flex;
		align-items: center;
		font-size: 16px;

		&:not(:last-child){

			margin-bottom: 11px;
		}

		._icon {

			fill: $colorOrange;
			width: 22px;
			height: 22px;
			margin-right: 12px;
		}

		i {

			font-style: normal;
		}
	}
	
}
