/*============================================================================
	#Investment
==============================================================================*/

.investment {

	padding-top: 90px;
	// margin-bottom: 115px;
	background-repeat: no-repeat;
	background-position: bottom -20px center;

	&__header {

		margin-bottom: 50px;
	}

	&__subtitle {

		font-size: 28px;
		font-weight: $semiBold;
		line-height: 1.28;
		color: $colorMaroon;
	}

	&__body {

		display: flex;
		margin: 0 -40px;
		padding-bottom: 100px;

	}

	&__col {

		flex: 0 0 calc(100% / 2 - 80px);
		margin: 0 40px;
	}

	&__typography {

		p {

			line-height: 24px;
		}

		&._white {

			color: white;
		}
	}

	&__footer {
		display: flex;
		justify-content: flex-end;
	}

	&__yt-player {
		margin-right: -60px;
		width: 820px;
		padding: 30px 30px 90px 30px;
		background: white;

		@media (max-width: 1440px) {
			margin-right: -70px;
		}
	}
}