/*============================================================================
	#Wishlist Page
==============================================================================*/

.whishlist-page {

	&__header {

		height: 153px;
		background-repeat: no-repeat;
		background-position: center;

		.container {

			display: flex;
			align-items: center;
			height: 100%;
		}
	}

	&__title {

		color: white;
		text-transform: uppercase;
		font-weight: $semiBold;
		font-size: 36px;
		padding: 0 15px;
		margin-bottom: 5px;
	}

	&__options {

		padding: 30px 0;
	}

	.section-typography {

		padding: 70px 0 65px;
	}
}