/*============================================================================
	#Button
==============================================================================*/

.button,
.button-primary,
.button-secondary {
	display: inline-block;
	border: 0;
	background: none;
	padding: 0;
	text-decoration: none;
	text-align: center;
	color: $colorFont;
	cursor: pointer;
	
}

.button-primary {

	background: $colorOrange;
	font-size: 18px;
	line-height: 24px;
	color: white;
	font-weight: $semiBold;
	padding: 15px 30px 16px;
	transition: all .2s ease-out;

	&:hover {

		color: #7f003e;
		background: #f5d1b1;
	}
}

.button-primary._medium {

	border-radius: 4px;
	padding: 12px 25px 14px;
}

.swiper-button {

	width: 40px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 100%;
	border: 1px solid $colorOrange;

	._icon {

		height: 14px;
		width: 8px;
		margin-left: 2px;
		fill: $colorOrange;
		transition: transform .2s ease-out, fill .2s ease-out;
	}

	&._prev {

		transform: rotate(180deg);
	}

	&._disabled {

		cursor: default;
		opacity: 0.4;
	}

	&:not(._disabled):hover {
	
		._icon {

			fill: $colorMaroon;
			transform: translateX(3px);
		}

	}
}