/*============================================================================
	#For Clients
==============================================================================*/
.clients-services {

	.container {

		position: relative;

		&::after {

			content: '';
			position: absolute;
			top: calc(50% - 50px);
			left: 50%;
			width: 1340px;
			height: 1095px;
			transform: translate(-50%,-50%);
			background: url('../images/for-clients/service-bg.jpg') no-repeat center; 
			// z-index: -1;
		}
	}

	&__header {

		padding-left: 570px;
		margin-bottom: 95px;
	}

	&__title {

		font-size: 48px;
		font-weight: $regular;
		color: $colorMaroon;
		position: relative;
		z-index: 1;
	}

	&__body {

		padding-left: 480px;
		position: relative;
		z-index: 1;

		&::after {

			content: '';
			position: absolute;
			top: -55px;
			right: -310px;
			width: 1194px;
			height: 355px;
			background: url('../images/for-clients/service-bg-texture.jpg');
			background-repeat: no-repeat;
			background-position: center;
			z-index: -1;
		}

		&::before {

			content: '';
			position: absolute;
			height: 50px;
			width: 1610px;
			left: -80px;
			background: $colorMaroon;
			bottom: -82px;
			z-index: -1;

		}
	}

	&__typography {

		max-width: 650px;
		font-size: 18px;
		line-height: 24px;

		strong {

			font-size: 20px; 
			position: relative;

			&::before {

				content: '';
				width: 42px;
				height: 30px;
				position: absolute;
				top: -5px;
				left: -55px;
				background: url('../icons/checkmark.svg') no-repeat;
				background-position: center;
			}
		}
	}

	&__media {
		
	}
	
	&__background-image {

		position: absolute;
		bottom: -34px;
    	left: -65px;
		z-index: 1;
	}

	&__image {

		position: absolute;
		bottom: 95px;
    	left: 115px;
		z-index: 2;
	}
}

.clients-about {
	padding-top: 165px;

	&__body {

		padding-left: 110px;
		margin: 0 -30px;
		display: flex;
		position: relative;
		z-index: 1;
	}

	&__col {

		margin: 0 30px;
		flex: 0 0 calc(100% / 2 - 60px); 
		max-width: calc(100% / 2 - 60px); 

		&._left {

			margin-top: 140px;
		} 
	}

	&__typography {

		color: #2b0317;

		h3 {

			color: #2b0317;
			font-weight: $semiBold;
			text-align: center;
			margin: 0 0 25px 0;
		}

		h4 {
			font-weight: $semiBold;		
			margin-bottom: 20px;
		}

		p {
			line-height: 24px;
		}
	}

	&__typography {

		&._small {

			padding-left: 70px;
			margin-bottom: 60px;
		}

		&._strong {

			strong {
				font-size: 18px;
			}

			p {
				margin-bottom: 35px;
				
				&:last-child {

					margin-bottom: 0;
				}
			}
		}
	}
}

.scroll-section {

}

.scrolled-container {

	position: relative;
	z-index: 20;
	display: none;
	pointer-events: none;

	&._fixed {

		position: fixed;
		top: 200px;
		left: 0;
		width: 100%;
		display: block;
	}

	&__image-wrapper {

		width: 640px;
		height: 600px;
		margin-left: -197px;
		position: relative;
	}

	&__image {

		width: 100%;
		height: 100%;
		object-fit: cover;
		position: absolute;
		top: 0;
		left: 0;
		transition: opacity 0.2s ease-out;

		&._hide {

			opacity: 0;
			transition: opacity 0.2s ease-out;
		}
	}

	._image-1 {

		transition: opacity 0.2s ease-out;
	}

	._image-2 {


		transition: opacity 0.2s ease-out;
	}
	
}

.scroll-block {

	position: relative;
	z-index: 1;
	padding: 80px 0;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	overflow: hidden;

	&._rent {

		background-position: top 240% center;
		background-size: 75%;
	} 

	&._faq {

		background-color: #fefcf8;

		.faq {

			position: relative;
			margin-top: 40px;
			// z-index: 1;

			&::before {

				content: '';
				position: absolute;
				z-index: -1;
				top: -130px;
				left: 40%;
				transform: translateX(-50%);
				width: 1140px;
				height: 915px;
				background: url('../images/for-clients/faq-bg.jpg') no-repeat center;
			}
		}
	}


	.container {

		display: flex;
	}

	&__media {

		flex: 0 0 42%;
		max-width: 42%;
		padding-right: 35px;
		display: flex;
		justify-content: flex-end;
	}

	&__image-wrapper {

		width: 640px;
		height: 600px;
		flex: 0 0 640px;
	}

	&__image {

		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__content {

		flex: 0 0 58%;
		max-width: 58%;
	}

	&__header {

		margin-bottom: 25px;
		padding: 0 15px;
	}

	&__title {

		font-size: 34px;
		font-weight: $semiBold;
		color: $colorMaroon;
	}

	&__typography {

		color: $colorMaroon;
		line-height: 24px;
		padding: 0 0 0 15px;
		// font-size: 15px;
		
	}

	&__footer {
		margin-top: 40px;
		display: flex;
		justify-content: center;

		&._decorated {

			flex-direction: column;
			align-items: center;
			text-align: center;
			padding: 30px 25px 40px;
		}

		&._column {

			flex-direction: row;

			.scroll-block__footer-title {

				font-size: 20px;
				line-height: 28px;
				text-align: left;
				margin-right: 20px;
				margin-bottom: 0;
			}
		}

		.button-primary {

			flex: 0 0 auto;
		}
		
	}

	&__footer-title {

		font-size: 24px;
		line-height: 30px;
		font-weight: $semiBold;
		color: #2b0317;
		margin-bottom: 20px;
	}
}

.faq {

	background: rgba(247, 241, 235, 0.7);

	&__container {

		padding: 0 20px;
	}
}

.faq-item {

	&__button {

		font-size: 20px;
		font-weight: $semiBold;
		line-height: 28px;
		color: $colorMaroon;
		padding: 15px 10px;
		border-bottom: 2px solid #c6b7b8;
		width: 100%;
		text-align: left;
		padding-right: 50px;
		position: relative;
		opacity: 1;
		transition: color 0.4s cubic-bezier(0.075, 0.82, 0.165, 1),
					opacity 0.2s ease-out;

		&._active {

			// color: $colorOrange;
			font-weight: $bold;

			&::after {

				transform: rotate(0deg);
			}
		}

		&::after,
		&::before {

			content: '';
			position: absolute;
			top: 50%;
			right: 10px;
			width: 21px;
			height: 3px;
			background: #feb117;
			transition: transform 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
		}

		&::after {

			transform: rotate(90deg);
		}

		&:not(._active):hover {

			opacity: 0.6;
		} 
	}

	&__content {

		display: none;
		padding: 0 20px 10px;
		border-bottom: 2px solid #c6b7b8;
		color: #2b0317;
		font-size: 15px;
		line-height: 24px;
		
	}

	&:last-child {

		.faq-item__button {

			border-color: transparent;

			&._active {
				border-color: #c6b7b8;
			}
		}

		.faq-item__content {

			border-color: transparent;
		}
	}
}

.clients-page {

	background-repeat: no-repeat;
	background-position: top 0px center;

	&__header {

		padding-top: 50px;
		padding-bottom: 50px;

		.container {

			position: relative;
		}
	}

	&__title {

		font-size: 40px;
		font-weight: $bold;
		line-height: 65px;
		text-transform: uppercase;
		color: white;
		padding: 0 110px;
	}

	&__figure {

		margin: 0 ;
		position: absolute;
		right: 40px;
    	top: 25px;

	}

	&__services {

		padding-top: 150px;
		padding-bottom: 90px;
		margin-bottom: 10px;
	}

	&__scroll-section {

		
	}
}